body {
  color: #40514e;
  margin: 0;
  text-align: center;
  font-family: "Merryweather", serif;
  font-size: 150%;
  background-color: #e4f9f5;
}



.top-container {
  height: 100vh;
  background-color: #e4f9f5;
  position: relative;
  padding-top: 100px;
}

.mountain-img {
  width: 40%;
}

.top-cloud {
  position: absolute;
  right: 300px;
  top: 40px;
  width: 15%;
}

.bottom-cloud {
  position: absolute;
  left: 250px;
  top: 400px;
  bottom: 300px;
}

.pro {
  text-decoration: underline;
}

.skill-row {
  display: grid;
  grid-template-columns: 1fr 2fr;
  width: 80%;
  margin: 80px 50px;
  text-align: left;
}

.image {
  width: 30%;
  float: right;
  margin-right: 5px;
  padding: 0px 50px 0px 0px;
}

.profile-img {
    width: 15%;
    border-radius: 50%;
    box-shadow: 1px 1px 2px black, 0 0 25px #66bfbf, 0 0 5px darkblue;
  }

  .intro {
    width: 32%;
    margin: auto;
  }
.btn {
  font-family: "Merryweather", serif;
  color: #ffffff;
  background: #349bdb;
  padding: 10px 20px 10px 20px;
  font-size: 25px;
  border-radius: 8px;
  text-decoration: none;
}

.btn:hover {
  background: #30e3cb;
  text-decoration: none;
  color: #40514e;
}

.contact-msg {
  margin: 40px auto 60px;
}

.contact-me {
  margin: 0px auto 20px;
}

.bottom-container {
  background-color: #66bfbf;
  padding: 50px 0 20px;
}

.copyright-msg {
  color: #eaf6f6;
  font-size: 1rem;
  padding: 25px 0;
}

.certificate-row {
  font-size: 1.2rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 80%;
  margin:  75px 75px 80px ;
  text-align: left;
}

.img {
  width: 30%;
  float: right;
  margin-right: 5px;
  padding: 0px 50px 0px 0px;
}

.img:hover {
  -webkit-transform: scale(2);
          transform: scale(2);
}

.experience-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.exp {
  display: inline-block;
  text-align: left;
  width: 20%;
  padding: 50px 10px 10px 150px;
}

.education-row {
    display: grid;
    grid-template-columns: 2fr 1fr;
    text-align: left;
    justify-content: start;
  }
  
  .edu-left {
    width: 40%;
  }

  .edu-right {
    position: relative;
    bottom: 45px;
    width: 20%;
  }

  .edu-left, .edu-right{
    display: inline-block;
    text-align: left;
    margin: 30px 10px 10px;

  }
  
