.certificate-row {
  font-size: 1.2rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 80%;
  margin:  75px 75px 80px ;
  text-align: left;
}

.img {
  width: 30%;
  float: right;
  margin-right: 5px;
  padding: 0px 50px 0px 0px;
}

.img:hover {
  transform: scale(2);
}
